import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { GlobalVariable } from '../utils/global';

@Injectable()
export class AuthService {

	authToken: any;
	user: any;

  	constructor(private http:Http) { }

  	registerUser (user) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

  		return this.http.post(GlobalVariable.BASE_API_URL+'users/register', user, {headers: headers})
  			.map(res => res.json());
  	}

  	authenticateUser (user) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

  		return this.http.post(GlobalVariable.BASE_API_URL+'users/authenticate', user, {headers: headers})
  			.map(res => res.json());
  	}

  	getProfile () {
  		let headers = new Headers();
  		this.loadToken();
  		headers.append('Authorization', this.authToken);
  		headers.append('Content-Type', 'application/json');

  		return this.http.get(GlobalVariable.BASE_API_URL+'users/profile', {headers: headers})
  			.map(res => res.json());
  	}

    updateUser (user) {
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');

      return this.http.post(GlobalVariable.BASE_API_URL+'users/update/'+user.is, user, {headers: headers})
        .map(res => res.json());
    }

  	storeUserData (token, user) {
  		localStorage.setItem('id_token', token);
  		localStorage.setItem('user', JSON.stringify(user));
  		this.authToken = token;
  		this.user = user;
  	}

    getCurrentUser() {
      return this.user;
    }

  	loadToken () {
  		const token = localStorage.getItem('id_token');
  		this.authToken = token;
  	}

  	loggedIn () {
  		return tokenNotExpired('id_token');
  	}

  	logout () {
  		this.authToken = null;
  		this.user = null;
  		localStorage.clear();
  	}
}



