import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { BlogComponent } from './components/blog/blog.component';
import { BlogdetailComponent } from './components/blogdetail/blogdetail.component';
import { ContactComponent } from './components/contact/contact.component';
import { FooterComponent } from './components/footer/footer.component';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { ProfileComponent } from './components/profile/profile.component';
import { RegisterComponent } from './components/register/register.component';
import { SafehtmlPipe } from './pipe/safehtml.pipe';
import { TruncatePipe } from './pipe/truncate.pipe';

import { FlashMessagesModule } from 'angular2-flash-messages';
import { AuthGuard } from './guards/auth.guard';

import { ValidateService } from './services/validate.service';
import { AuthService } from './services/auth.service';
import { UploadService } from './services/upload.service';
import { BlogService } from './services/blog/blog.service';
import { TagService } from './services/tag/tag.service';
import { SliderService } from './services/slider/slider.service';
import { ContactService } from './services/contact/contact.service';

const appRoutes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'blog', component: BlogComponent},
  {path: 'profile', component: ProfileComponent, canActivate:[AuthGuard]},
  {path: 'blog/detail/:_id', component: BlogdetailComponent},
  {path: 'blog/search/:q', component: BlogComponent},
  {path: 'blog/search', component: BlogComponent},
  {path: 'blog/page', component: BlogComponent},
  {path: 'blog/page/:p', component: BlogComponent},
  {path: 'contact', component: ContactComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    BlogComponent,
    BlogdetailComponent,
    ContactComponent,
    FooterComponent,
    HomeComponent,
    LoginComponent,
    NavbarComponent,
    ProfileComponent,
    RegisterComponent,
    SafehtmlPipe,
    TruncatePipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule,
  ],
  providers: [
    ValidateService,
    AuthService,
    AuthGuard,
    UploadService,
    BlogService,
    TagService,
    SliderService,
    ContactService
  ],

  bootstrap: [AppComponent]
})
export class AppModule { }



