import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GlobalVariable } from '../utils/global';

@Injectable()
export class UploadService {
	private formData: FormData = new FormData();
  	constructor(private http:Http) { }

  	isImage(fileType) {
      	if (fileType.indexOf("image") !== -1) return true;
      	return false;
    }

  	uploadFile (file, id) {
  		let hash = id || file.name;
  		const formData = new FormData();
        formData.append('avatar', file, hash);
        return this.http.post(GlobalVariable.BASE_API_URL+'users/upload', formData).map(res => res.json());

	}
	  
	upload (options) {
		let hash = options.hash || options.file.name;
		const fd = new FormData();
		fd.append(options.name, options.file, hash );

		return this.http.post(GlobalVariable.BASE_API_URL+'uploads', fd)
						.map(res => res.json());
	}
}



