import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, NavigationEnd } from '@angular/router';
import { BlogService } from '../../services/blog/blog.service';
import { TagService } from '../../services/tag/tag.service';

@Component({
  selector: 'app-blogdetail',
  templateUrl: './blogdetail.component.html',
  styleUrls: ['./blogdetail.component.css']
})
export class BlogdetailComponent implements OnInit {
	blogdetail: any;
	body:       String; // body comment
	user:       any;
  q:          String;
  tags:       any;
  newBlogs:   any;

  	constructor(private activatedRoute: ActivatedRoute,
  				private blogService:BlogService,
  				private router:Router,
          private tagService:TagService) { }

  	ngOnInit() {

			// scroll top after navigate
			this.router.events.subscribe((evt) => {
				if (!(evt instanceof NavigationEnd)) {
						return;
				}
				window.scrollTo(0, 0)
			});

  		// subscribe to router event
    	this.activatedRoute.params.subscribe((params: Params) => {
      	let _id = params['_id'];
      	this.blogService.getBlogById(_id).subscribe(data => {
      		this.blogdetail = data;
      	})
    	});

    	// get current user
      if(localStorage.getItem('user')){
        this.user = JSON.parse(localStorage.getItem('user'));
      }else{

        let anonymous = {
          name: 'anonymous',
          avatar: '5ace2fbabec47220a0d3d0d2.png',
        }
        this.user = anonymous;
      }

      // get Tags
      this.tagService.getTags().subscribe(data => {
        this.tags = data;
      });

      // get new Blogs
      this.blogService.getNewBlogs(5).subscribe(data => {
        this.newBlogs = data.data;
      });
  	}

  	// Su kien keydown cua textarea comment
  	onKeyDown(event:any){
  		if (event.keyCode == 13){
  			if (event.ctrlKey) {
  				this.body += String.fromCharCode(13, 10);
  			} else {
  				this.onCommentSubmit();
  			}
  		}
  	}

  	// Su kien khi submit form comment
  	onCommentSubmit() {
		if (!this.body) {
			return;
		}

		let newComment = {
			body: this.body,
      userid: this.user.is,
      avatar: this.user.avatar,
      author: this.user.name,
		}
		let _id = this.blogdetail._id;

		this.blogService.addComment(_id, newComment).subscribe(blog => {
			this.blogdetail = blog;
			this.body = '';
		}, 
		err =>{
			console.log(err);
		})
  	}

  	// Get Older Post
  	prevBlog () {
  		this.blogService.prevBlog(this.blogdetail.date).subscribe(data => {
  			if(data) {
  				this.router.navigate(['/blog/detail/'+data._id]);	
  			}
	  	},
	  	err =>{
	  		console.log(err);
	  	});
  	}

  	// Get Newer Post
  	nextBlog () {
  		this.blogService.nextBlog(this.blogdetail.date).subscribe(data => {
  			if(data) {
  				this.router.navigate(['/blog/detail/'+data._id]);	
  			}
	  	},
	  	err =>{
	  		console.log(err);
	  	});
  	}

    // Search Blog
    onSearchSubmit() {
      this.router.navigate(['/blog/search/', this.q]);  
    }

    // Search Blog By Tag
    searchTag(tagValue) {
      this.router.navigate(['/blog/search/', tagValue]);  
    }

}



