import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { BlogService} from '../../services/blog/blog.service';
import { TagService } from '../../services/tag/tag.service';
import { isNumber } from 'util';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {
  blogs:   any;
  q:       String;
  p:       Number;
  tags:    Array<String>;
  newBlogs: any;
  count:  Number[];

	constructor(private router:Router,
				private flashMessagesService:FlashMessagesService,
				private blogService:BlogService,
        private activatedRoute: ActivatedRoute,
        private tagService:TagService) { }

	ngOnInit() {
    // If isset params q => search by q Else Show all blogs
    this.activatedRoute.params.subscribe((params: Params) => {
      this.q = params['q'] ? params['q'].trim() : '';
      this.p = params['p'] ? params['p'] : 1;

      // search blog
      if (this.q) {
        this.blogService.searchBlogs(this.q).subscribe(res => {
          this.blogs = res;
        });
      } 

      // get all blogs
      else {
        this.blogService.getBlogs(this.p).subscribe(res => {
          this.blogs = res.data;
          if(res.pagination){
            this.count = new Array(Math.ceil(parseFloat(res.pagination.count)));
          }
            
        });
      }

      // get new logs => limit 5
      this.blogService.getNewBlogs(5).subscribe(data => {
        this.newBlogs = data.data;
        console.log(this.newBlogs);
      });

    });

    // Init Tags
    this.tagService.getTags().subscribe(data => {
      this.tags = data;  
    });
	}

  // Search Blog
  onSearchSubmit() {
    this.router.navigate(['/blog/search/', this.q]);
  }
  // Search Blog By Tag
  searchTag(tagValue) {
    this.router.navigate(['/blog/search/', tagValue]);  
  }

  createRange(number){
    var items: number[] = [];
    for(var i = 1; i <= number; i++){
       items.push(i);
    }
    return items;
  }
}



