import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GlobalVariable } from '../../utils/global';

@Injectable()
export class BlogService {
  	constructor(private http:Http) {
	}

  	getBlogs (page) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

  		return this.http.get(GlobalVariable.BASE_API_URL+'blogs?page='+page, {headers: headers})
  			.map(res => res.json());
  	}

    searchBlogs (q) {
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');

      return this.http.get(GlobalVariable.BASE_API_URL+'blogs/search/'+q, {headers: headers})
        .map(res => res.json());
    }

	getNewBlogs (limit) {
		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

  		return this.http.get(GlobalVariable.BASE_API_URL+'blogs?limit='+limit, {headers: headers})
  			.map(res => res.json());
	}

  	getBlogById (_id) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

  		return this.http.get(GlobalVariable.BASE_API_URL+'blogs/'+_id, {headers: headers})
  			.map(res => res.json());
  	}

  	addComment (_id, newComment) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');

  		return this.http.post(GlobalVariable.BASE_API_URL+'blogs/'+_id+'/comments/', newComment, {headers: headers})
  			.map(res => res.json());
  	}

  	prevBlog (date) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');
  		let request = {date: date};
  		return this.http.post(GlobalVariable.BASE_API_URL+'blogs/prev', request, {headers: headers})
  			.map(res => res.json());
  	}

  	nextBlog (date) {
  		let headers = new Headers();
  		headers.append('Content-Type', 'application/json');
  		let request = {date: date};
  		return this.http.post(GlobalVariable.BASE_API_URL+'blogs/next', request, {headers: headers})
  			.map(res => res.json());
  	}
}




