import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { UploadService } from '../../services/upload.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
	user: any;
  file:File;

	@ViewChild('elUpload') elUpload: ElementRef;
  @ViewChild('avatar') avatar: ElementRef;

  	constructor(private authService:AuthService,
                private uploadService:UploadService,
                private flashMessage:FlashMessagesService) { }

  	ngOnInit() {
  		this.authService.getProfile().subscribe(profile => {
  			this.user = profile.user;
        if (!this.user.avatar) {
          if(this.avatar)
            this.avatar.nativeElement.src = 'https://x1.xingassets.com/assets/frontend_minified/img/users/nobody_m.original.jpg';
        }
  		},
  		err => {
  			return false;
  		})
  	}

  	openUpload () {
      this.elUpload.nativeElement.click();
  	}

    changeListener($event) : void {
        this.readThis($event.target);
    }

    readThis(inputValue: any): void {
        this.file = inputValue.files[0];
        var myReader:FileReader = new FileReader();

        myReader.onloadend = (e) => {
          if (this.uploadService.isImage(this.file.type)){
            this.avatar.nativeElement.src = myReader.result;
          }
        }

        myReader.readAsDataURL(this.file);
    }

    onProfileSubmit() {
      if (this.file) {
        let user = localStorage.getItem('user');
        let newuser = JSON.parse(user);

        this.uploadService.uploadFile(this.file, newuser.is).subscribe(data => {
          if (data.success) {
            newuser.avatar = data.filename;
            this.authService.updateUser(newuser).subscribe(data => {
              if(data.success){
                this.flashMessage.show('Update successfully', {cssClass: 'alert-success', timeout: 3000});
              }else{
                this.flashMessage.show('Update failed', {cssClass: 'alert-danger', timeout: 3000});
              }
            })
          }
        },
        err => {
          console.log(err);
        });
      }
    }
}



